import {ERROR_NA, ERROR_VALUE, errorObj} from '../../calc_utils/error_config'
import * as utils from '../../calc_utils/helper'
import * as information from './information'
import {OnlyNumberExpFunction} from "../../calc_data_proxy/exp_function_warp";

/**
 *  AND  TRUE
 * @returns {boolean}
 * @constructor
 */
export function AND() {
  let args = utils.flatten(arguments);
  let result = 'true';
  for (let i = 0; i < args.length; i++) {
    if (!args[i]) {
      result = false;
    }
  }
  return result;
};

/**
 * index_num      index_num  1  254  1  254 
  index_num  1 CHOOSE  value1 2 CHOOSE  value2
  index_num  1  CHOOSE  #VALUE! 
  index_num 
 value1, value2, ...    Value1  1  254 
 CHOOSE  index_num  
 * @returns {*|Error}
 * @constructor
 */
function CHOOSE_() {
  if (arguments.length < 2) {
    return Error(ERROR_NA);
  }
  let index = arguments[0];
  if (index < 1 || index > 254) {
    return Error(ERROR_VALUE);
  }
  if (arguments.length < index + 1) {
    return Error(ERROR_VALUE);
  }
  return arguments[index];
};
export const CHOOSE = new OnlyNumberExpFunction(CHOOSE_)

exports.FALSE = function() {
  return false;
};

/**
 *
 * @param {string}test 
 * @param {number/string}then_value logical_test  TRUE 
 * @param {number/string}otherwise_value logical_test  FALSE 
 * @returns {*}
 * @constructor
 */
export function IF(test, then_value, otherwise_value) {
  return test ? then_value : otherwise_value;
};

/**
 *
 * @param {number}value  
 * @param {number/string}valueIfError   : #N/A#VALUE!#REF!#DIV/0!#NUM!#NAME #NULL!
 * @returns {*}
 * @constructor
 */
export function IFERROR(value, valueIfError) {
  if (information.ISERROR_(value)) {
    return valueIfError;
  }
  return value;
};

exports.IFNA = function(value, value_if_na) {
  return value === errorObj.ERROR_NA ? value_if_na : value;
};

exports.NOT = function(logical) {
  return !logical;
};

exports.OR = function() {
  let args = utils.flatten(arguments);
  let result = false;
  for (let i = 0; i < args.length; i++) {
    if (args[i]) {
      result = true;
    }
  }
  return result;
};

exports.TRUE = function() {
  return true;
};

exports.XOR = function() {
  let args = utils.flatten(arguments);
  let result = 0;
  for (let i = 0; i < args.length; i++) {
    if (args[i]) {
      result++;
    }
  }
  return (Math.floor(Math.abs(result)) & 1) ? true : false;
};

exports.SWITCH = function () {
  let result;
  if (arguments.length > 0) {
    let targetValue = arguments[0];
    let argc = arguments.length - 1;
    let switchCount = Math.floor(argc / 2);
    let switchSatisfied = false;
    let defaultClause = argc % 2 === 0 ? null : arguments[arguments.length - 1];

    if (switchCount) {
      for (let index = 0; index < switchCount; index++) {
        if (targetValue === arguments[index * 2 + 1]) {
          result = arguments[index * 2 + 2];
          switchSatisfied = true;
          break;
        }
      }
    }

    if (!switchSatisfied && defaultClause) {
      result = defaultClause;
    }
  }
  if (result == undefined){
    return errorObj.ERROR_NA
  }
  return result;
};
