import exp from '../../utils/alphabet';
import { isHave } from '../../helper/check_value';

/**
 * @property {PreAction} preAction
 */
export class CalcRowsProxy { // rows
  constructor(rows, preAction) {
    this.rows = rows;
    this.preAction = preAction;
  }

  // workbook
  updateWorkbook(workbook) {
    let { data } = this.rows;
    let name = data['name'];
    let need_calc_cells = this.preAction.findAllNeedCalcCell();//
    for (let i = 0; i < need_calc_cells.length; i++) {
      let cell_name = need_calc_cells[i];
      let zb = exp.expr2xy(cell_name);
      let cell = this.rows.getCell(zb[1], zb[0]);
      if (isHave(cell)) {
        workbook.Sheets[name][cell_name] = {
          v: cell.text,
          f: cell.formulas
        };//workbook
      }
    }
    return workbook;
  }

  rows2workbook() { // rowsworkbook
    let rows = this.rows
    let cells = rows._;
    let workbook = null;
    let { data } = rows;
    let name = data['name'];
    if (isHave(rows.name2SheetProxy)) {//rowsworkbookworkbookworkbook
      workbook = rows.name2SheetProxy;
    } else {
      workbook = {
        Sheets: {}
      };
      workbook.Sheets[name] = {};
    }
    //workbook
    Object.keys(cells)
      .forEach(ri => {
        Object.keys(cells[ri].cells)
          .forEach(ci => {
            let cell = rows.getCell(ri, ci);
            let cell_name = exp.xy2expr(ci, ri);
            if (isHave(cell)) {
              workbook.Sheets[name][cell_name] = {
                f: cell.formulas
              };
            }
          });
      });
    return workbook;
  }

  updateRowsByCalcCellArray(calcCellArray, rows){
    calcCellArray.map((calcCell) => {rows.setCell(...calcCell.getRiCi(),calcCell.getCellObjForRendering(), "assign")})
  }


  //workbookrows
  calcDoneToSetCells(workbook, rows) {
    let { data } = rows;
    let name = data['name'];
    let sheet = workbook.Sheets[name];
    Object.keys(sheet)
      .forEach(i => {
        let arg = exp.expr2xy(i);//
        if (isHave(sheet[i]) && isHave(sheet[i].v) && isHave(sheet[i].f)) {//workbookrows
          let cell = rows.getCell(arg[1], arg[0]);//
          if (!isHave(cell)) {
            cell = {};
          }
          cell.v = sheet[i].v
          cell.text = sheet[i].v.toString(); //text
          cell.formulas = sheet[i].f;
          rows.setCell(arg[1], arg[0], cell);
        }
      });
    rows.workbook = workbook;
  }
}
